import React from 'react';
import { motion } from 'framer-motion';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faPhone,
  faEnvelope,
  faMapMarkerAlt,
  faUser,
  faBriefcase,
  faGraduationCap,
  faCode,
  faFilePdf,
} from '@fortawesome/free-solid-svg-icons';
import { faLinkedinIn } from '@fortawesome/free-brands-svg-icons';

import useMousePosition from '../hooks/useMousePosition';
import './Sidebar.css';
import profileImage from './Amul.png';

const navItems = [
  { id: 'about', icon: faUser, label: 'About Me' },
  { id: 'experience', icon: faBriefcase, label: 'Experience' },
  { id: 'skills', icon: faCode, label: 'Skills' },
  { id: 'education', icon: faGraduationCap, label: 'Education' },
];

const contactInfo = [
  { icon: faPhone, text: '0420 652 360' },
  { icon: faEnvelope, text: 'regmiamul@gmail.com' },
  { icon: faLinkedinIn, text: 'linkedin.com/in/amulregmi' }
];

const quickStats = [
  { value: '5+', label: 'Years Experience' },
  { value: '100%', label: 'Commitment' }
];

const Sidebar = ({ activeSection, setActiveSection, darkMode, toggleDarkMode }) => {
  const { targetRef } = useMousePosition();

  const handleNavClick = (sectionId) => {
    setActiveSection(sectionId);
  };

  const handleDownloadPDF = () => {
    import('html2pdf.js').then((html2pdf) => {
      const element = document.querySelector('.homepage .section-content');
      const opt = {
        margin: 1,
        filename: 'Amul_Regmi_Resume.pdf',
        image: { type: 'jpeg', quality: 0.98 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: 'in', format: 'letter', orientation: 'portrait' }
      };
      html2pdf.default().set(opt).from(element).save();
    });
  };

  return (
    <motion.aside className="sidebar" ref={targetRef}>
      <div className="sidebar-inner">
        {/* Profile Card */}
        <motion.div className="profile-card">
          <div className="avatar-container">
            <motion.div
              className="avatar"
              whileHover={{ scale: 1.05 }}
              transition={{ type: 'spring', stiffness: 400, damping: 10 }}
            >
              <img src={profileImage} alt="Amul Regmi" className="avatar-face" />
            </motion.div>
          </div>

          <div className="profile-info">
            <h1>Amul Regmi</h1>
            <p className="title">IT Support Specialist</p>
            <div className="location-chip">
              <FontAwesomeIcon icon={faMapMarkerAlt} />
              <span>Canberra, ACT</span>
            </div>
          </div>

          {/* Contact Info */}
          <div className="contact-info">
            {contactInfo.map((contact, i) => (
              <motion.div key={i} className="contact-item">
                <motion.div className="contact-icon">
                  <FontAwesomeIcon icon={contact.icon} />
                </motion.div>
                <span>{contact.text}</span>
              </motion.div>
            ))}
          </div>

          {/* Quick Stats */}
          <div className="quick-stats">
            {quickStats.map((stat, i) => (
              <motion.div key={i} className="stat-item">
                <div className="stat-value">{stat.value}</div>
                <div className="stat-label">{stat.label}</div>
              </motion.div>
            ))}
          </div>
        </motion.div>

        {/* Navigation */}
        <nav className="sidebar-nav">
          {navItems.map((item) => (
            <motion.button
              key={item.id}
              className={`nav-item ${activeSection === item.id ? 'active' : ''}`}
              onClick={() => handleNavClick(item.id)}
              aria-current={activeSection === item.id ? 'page' : undefined}
              whileHover={{ x: 10 }}
              whileTap={{ scale: 0.95 }}
            >
              <div className="nav-icon">
                <FontAwesomeIcon icon={item.icon} />
              </div>
              <span>{item.label}</span>
            </motion.button>
          ))}

          {/* Download Resume Button
          <motion.button
            className="nav-item pdf-export"
            onClick={handleDownloadPDF}
            whileHover={{ x: 10 }}
            whileTap={{ scale: 0.95 }}
          >
            <div className="nav-icon">
              <FontAwesomeIcon icon={faFilePdf} />
            </div>
            <span>Download Resume</span>
          </motion.button> */}
        </nav>

        {/* Dark Mode Toggle
        <div className="theme-toggle">
          <label className="switch">
            <input
              type="checkbox"
              checked={darkMode}
              onChange={toggleDarkMode}
              aria-label="Toggle dark mode"
            />
            <span className="slider round"></span>
            <span className="mode-label">{darkMode ? 'Dark Mode' : 'Light Mode'}</span>
          </label>
        </div> */}
      </div>
    </motion.aside>
  );
};

export default Sidebar;