import React from 'react';
import useMousePosition from '../hooks/useMousePosition';
import {
  educationData,
  experienceData,
  skillsList,
  skillsData,
  certificationsData
} from '../data/content';
import './Homepage.css';

const Homepage = ({ activeSection }) => {
  const { targetRef } = useMousePosition();

  return (
    <main className="homepage" ref={targetRef}>
      <div className="section-container">
        {/* ABOUT ME */}
        {activeSection === 'about' && (
          <div className="section-content">
            <h2>Professional Summary</h2>
            <p>
            Motivated and experienced IT support specialist with over 3 years of hands-on experience across system administration, field engineering, and multi-site technical support. Currently serving as a Level 2 IT Support Specialist at Viva Leisure, with deep knowledge of internal systems, ongoing IT projects, and infrastructure. Adept at troubleshooting, project execution, and staff training. Seeking to transition into a System Administrator role to continue contributing to organizational growth and technological innovation.

            </p>
            <h3>Key Skills</h3>
            <ul className="competencies">
              {skillsList.map((skill, i) => (
                <li key={i}>{skill}</li>
              ))}
            </ul>
          </div>
        )}

        {/* TECHNICAL SKILLS */}
        {activeSection === 'skills' && (
          <div className="section-content">
            <div className="skills-grid">
              {skillsData.map((category, index) => (
                <div className="skill-category" key={index}>
                  <h3>{category.title}</h3>
                  <ul>
                    {category.items.map((item, idx) => (
                      <li key={idx}>{item}</li>
                    ))}
                  </ul>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* EXPERIENCE */}
        {activeSection === 'experience' && (
          <div className="experience-timeline">
            {experienceData.map((exp, index) => (
              <div className="experience-item" key={index}>
                <h3>{exp.title}</h3>
                <div className="company">{exp.company}</div>
                <div className="duration">{exp.duration}</div>
                <ul className="achievements">
                  {exp.achievements.map((point, idx) => (
                    <li key={idx}>{point}</li>
                  ))}
                </ul>
              </div>
            ))}
          </div>
        )}

        {/* EDUCATION & CERTIFICATIONS */}
        {activeSection === 'education' && (
          <div className="education-list">
            {educationData.map((edu, index) => (
              <div className="education-item" key={index}>
                <h3>{edu.degree}</h3>
                <div className="institution">{edu.institution}</div>
                <div className="duration">{edu.duration}</div>
                <div className="grade">{edu.status}</div>
                <div className="skills">
                  <h4>Skills Gained:</h4>
                  <ul>
                    {edu.skills.map((skill, idx) => (
                      <li key={idx}>{skill}</li>
                    ))}
                  </ul>
                </div>
              </div>
            ))}

            <div className="certifications">
              <h3>Certifications</h3>
              <ul>
                {certificationsData.map((cert, index) => (
                  <li key={index}>{cert.name}</li>
                ))}
              </ul>
            </div>
          </div>
        )}
      </div>
    </main>
  );
};

export default Homepage;