import React, { useState, useEffect } from 'react';
import { AnimatePresence } from 'framer-motion';
import Sidebar from './components/Sidebar';
import Homepage from './components/Homepage';
import './App.css';

const App = () => {
  const [activeSection, setActiveSection] = useState('about');
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  // Load theme preference
  useEffect(() => {
    const savedTheme = localStorage.getItem('darkMode') === 'true';
    setDarkMode(savedTheme);
    if (savedTheme) document.body.classList.add('dark-theme');
  }, []);

  // Apply theme dynamically
  useEffect(() => {
    if (darkMode) {
      document.body.classList.add('dark-theme');
    } else {
      document.body.classList.remove('dark-theme');
    }
    localStorage.setItem('darkMode', darkMode);
  }, [darkMode]);

  // Detect mobile
  useEffect(() => {
    const checkIfMobile = () => {
      const mobile = window.innerWidth < 1024;
      setIsMobile(mobile);
      if (!mobile) setSidebarOpen(true);
    };

    checkIfMobile();
    window.addEventListener('resize', checkIfMobile);
    return () => window.removeEventListener('resize', checkIfMobile);
  }, []);

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
    if (isMobile) document.body.style.overflow = sidebarOpen ? 'auto' : 'hidden';
  };

  return (
    <div className={`app-container ${isMobile && sidebarOpen ? 'sidebar-open' : ''}`}>
      {/* Mobile Header */}
      {isMobile && (
        <header className="mobile-header">
          <button 
            className="menu-toggle" 
            onClick={toggleSidebar}
            aria-label={sidebarOpen ? "Close menu" : "Open menu"}
            aria-expanded={sidebarOpen} 
          >
            <div className="menu-icon">
              <div className={`bar bar1 ${sidebarOpen ? 'open' : ''}`}></div>
              <div className={`bar bar2 ${sidebarOpen ? 'open' : ''}`}></div>
              <div className={`bar bar3 ${sidebarOpen ? 'open' : ''}`}></div>
            </div>
          </button>
          <h1 className="mobile-title">Amul Regmi</h1>
        </header>
      )}

      <div className="layout-grid">
        {/* Sidebar Overlay */}
        <AnimatePresence>
          {isMobile && sidebarOpen && (
            <div className="sidebar-overlay" onClick={toggleSidebar} />
          )}
        </AnimatePresence>

        {/* Sidebar */}
        <div className={`sidebar-container ${sidebarOpen ? 'active' : ''}`}>
          <Sidebar
            activeSection={activeSection}
            setActiveSection={(section) => {
              setActiveSection(section);
              if (isMobile) setSidebarOpen(false);
            }}
            darkMode={darkMode}
            toggleDarkMode={() => setDarkMode(!darkMode)}
          />
        </div>

        {/* Main Content */}
        <main className="main-content">
          <Homepage activeSection={activeSection} />
        </main>
      </div>
    </div>
  );
};

export default App;